.profile_wrap {
  .profile_arrow {
    img {
      width: 10px;
      position: relative;
      top: 23px;
    }
  }
  .section01 {
    margin: 25px 0;
    .my_profile {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      figure {
        width: 130px;
      }

      figcaption {
        margin-top: 20px;
        font-size: 18px;
        color: #333;
        font-family: "AppleSDGothicNeoSB";
      }
    }
  }

  .section02 {
    margin-top: 30px;
    padding-bottom: 85px;
    height: auto;

    .in_wrap {
      display: flex;
      margin: 0 auto;
      width: 75%;
      overflow: hidden;
      justify-content: space-between;
      flex-wrap: wrap;

      .select_profile {
        width: 26%;
        margin-bottom: 10px;
      }
    }

    button {
      border: 0;
      outline: 0;
      border-radius: 30px;
      font-family: "AppleSDGothicNeoSB";
      width: 100%;
      height: 32px;
      line-height: 34px;
      text-align: center;
      font-size: 14px;
      background: #f5aa19;
      color: #fff;
      margin-top: 30px;
    }
  }
}
